


























import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";
import Attention from "@/components/Attention.vue";
import store from "@/store/index2";
import TimeInput from "@/components/money/TimeInput.vue";
import dayjs from "dayjs";
@Component({
    components: {TimeInput, Attention}
})
export default class NumberPad extends Vue {
    @Prop(String) readonly value!: string;
    @Prop(Object) readonly record!: ReceiptData
    @Prop(String) readonly showDateTime!: string
    @Prop(String) readonly currentTime!: string
    showAttention = "none"
    appear = "none"
    show = "none"
    get beautyTime(){
      if (this.currentTime==dayjs().format("YYYY-MM-DD")) {
        return "今天"
      }else {
        return dayjs(this.currentTime).format("YYYY-MM-DD")
      }
    }
    inputNotes(){
        this.appear = "block"
    }
    updateNotes(event: string){
        this.$emit("update:notes",event)
    }
    showDate(){
        this.$emit("update:showDateTime","show")
    }
    inputMoney(event: MouseEvent) {
        const number = (event.target as HTMLButtonElement).textContent;
        if (this.value.length >= 8) {
            if ( parseInt(this.value)>=100000) {
                this.show = "block"
            }
            return;
        }
        if (this.value === "0") {
            if (number && "0123456789".indexOf(number) >= 0) {
                this.$emit("update:value", number);
            }
            if (number === "."){
                this.$emit("update:value", this.value + number);
            }
        } else {
            if (this.value.indexOf(".") >= 0 && number === ".") {
                return;
            } else {
                this.$emit("update:value", this.value + number);
            }
        }
    }

    remove() {
        if (this.value.length === 1) {
            this.$emit("update:value", "0");
        } else {
            this.$emit("update:value", this.value.slice(0, -1));
        }
    }

    clear() {
        this.$emit("update:value", "0");
    }

    ok() {
        if (this.value === "0"){
            this.showAttention = "block"
        }else{
            store.createRecord(this.record)
            window.alert("记下一笔");
            this.$emit("update:value","0")
        }
    }
}
