














import Vue from "vue";
import {Component} from "vue-property-decorator";

@Component
export default class StatisticType extends Vue {
    type = "-"
    date = "day"
    changeType(type: string){
        this.type = type
        this.$emit("update:type",this.type)
    }
    changeDate(date: string){
        this.date = date
        this.$emit("update:date",this.date)
    }
}
