













import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";
import store from "@/store/index2";

@Component
export default class EditTagTop extends Vue {
    @Prop(String) readonly icon!: string;
    @Prop(String) readonly name!: string;
    @Prop(String) readonly title!: string;
    @Prop(String) readonly type!: string;

    show = "none";
    span = "";

    goBack() {
        this.$router.back();
    }
    popWarn(span: string){
        this.show = "block";
        this.span = span;
    }
    saveTag() {
        if (!this.name) {
            this.popWarn("标签名不能为空")
        } else if (!this.icon) {
            this.popWarn("请选择一个图标")
        } else {
            if (store.createTag(this.icon, this.name,this.type) === "duplicated"){
                this.popWarn("标签名已经存在")
            }else{
                store.createTag(this.icon, this.name,this.type);
                this.goBack()
                store.saveTag()
            }

        }
    }
}
